@import url("https://fonts.googleapis.com/css2?family=Lato:wght@900&family=Roboto:ital,wght@1,500&display=swap");

.header {
  min-height: 5vw;
  hr {
    border: 1px solid #223046;
  }
}

.headerInner {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  a {
    text-decoration: none;
  }
  h2 {
    font-family: "Lato", sans-serif;
    color: #223046;
    font-size: 1.6vw;
    cursor: pointer;
  }
}

.person {
  display: flex;
  justify-content: center;
  align-items: center;
  i {
    color: #223046;
    font-size: 1.6vw;
    cursor: pointer;
  }
}
